.image__block-first {
  margin-top: 0;
  margin-bottom: 120px;
  width: 100%;
  height: 927px;
  background-image: url("../img/imgonline-com-ua-Resize-DlRXsc4WVsD3h.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.image__block-first .best-place {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.image__block-first .best-place__circle {
  border-radius: 50%;
  border: 2px solid white;
  width: 80px;
  height: 80px;
  margin-top: 165px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  background: none;
  transition: all ease 0.5s;
}
.image__block-first .best-place__circle::before {
  transition: all 0.5s ease;
  content: "";
  width: 0%;
  height: 100%;
  background: #B85C38;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.image__block-first .best-place__circle__img {
  fill: white;
}
.image__block-first .best-place__circle:hover::before {
  width: 100%;
}
.image__block-first .best-place__circle:hover {
  transform: scale(0.9);
  background-color: #FF804E;
}
.image__block-first .main__title {
  color: white;
  margin-top: 40px;
  font-family: "Nunito";
  text-align: center;
  font-size: 72px;
  font-weight: 700;
}
.image__block-first .main__description {
  color: white;
  margin-top: 8px;
  font-family: "Nunito";
  text-align: center;
  font-size: 24px;
}
.image__block-first .main__button_block {
  margin-bottom: 400px;
  margin-top: 58px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.image__block-first .main__button_block_demo {
  font-family: "Nunito";
  font-size: 20px;
  padding: 18px 32px;
  background-color: #B85C38;
  border-radius: 40px;
  color: white;
  transition: all ease 0.5s;
}
.image__block-first .main__button_block_demo:hover {
  transform: scale(0.9);
  background-color: #FF804E;
}
.image__block-first .main__button_block_buy {
  margin-left: 24px;
  font-family: "Nunito";
  font-size: 20px;
  padding: 18px 32px;
